import { useState } from "react";
import { useHistory } from "react-router-dom";
import  axios from "axios";

function Login() {
    let history = useHistory();
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [unameerror, setUnameerror] = useState('');
    const [passworderror, setPassworderror] = useState('');
    const signIn = async () => {
        const res = await axios.get("https://reqres.in/api/login");
        localStorage.setItem('username',res.data.data[0].name);
        history.push("/");
        window.location.reload();
    }
    function submitHandler(event){
        setUnameerror('');
        setPassworderror('');
        if(username && password){
            signIn();
        }else{
            if(username === ''){
                setUnameerror('Username is required.');
            }
            if(password === ''){
                setPassworderror('Password is required.');
            }
        }
    }
    return (
        <div className="content-inner">
            <div className="row justify-content-center align-items-center">
                <div className="col-4">
                    <div className="card">
                        <div className="card-body">
                            <h3 className="card-title login-heading">Login</h3>
                            <form action="" autoComplete="off">
                                <div className="form-group">
                                    <input type="text" className="form-control" name="username" autoComplete="off" placeholder="Username" onChange={(event) => {setUsername(event.target.value);}} />
                                    <span className="text-danger">{unameerror}</span>
                                </div>
                                <div className="form-group">
                                    <input type="password" className="form-control" name="password" autoComplete="off" placeholder="Password" onChange={(event) => {setPassword(event.target.value);}} />
                                    <span className="text-danger">{passworderror}</span>
                                </div>
                                <button type="button" id="sendlogin" className="btn btn-primary" onClick={(event)=> submitHandler(event)}>login</button>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Login;